.trending {
  position: relative;
  padding: clamp(1rem, calc(0.8rem + 3.5vw), 5rem) 0;
  background-color: var(--color-gray-300);

  &__nav {
    @include flex-all-sb;
  }

  &__btns {
    @include flex-sb;

    position: relative;
  }

  &__btn {
    margin: 0 auto;
  }
}

.trending-slider {
  overflow: hidden;
  margin-bottom: clamp(1rem, calc(0.8rem + 3.5vw), 5rem);

  &__next,
  &__prev {
    right: 0;

    &:hover {
      background-color: var(--color-green);

      &::before {
        background-color: var(--color-white);
      }
    }
  }

  &__prev {
    right: 60px;
  }
}
